.TodoSearch
{
    background: #F9FBFC;
    border-radius: 2px;
    border: 2px solid #202329;
    margin: 0 24px;
    height:  64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0 5px 50px rgba(32, 35, 41, 0.25);
}
.TodoSearch::placeholder
{
    color: #A5A5A5;
    font-weight: 400;
}
.TodoSearch:focus
{
    outline-color: #61DAFA;
}