.CreateTodoButton
{
    background-color: #61DAFA;
    box-shadow: 0 5px 25px rgba(97, 218, 250, 0.5);
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    position: fixed;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;
    transition: 3s ease;
    z-index: 1;
}
.CreateTodoButton:hover
{
    transform: rotate(224deg);
    background-color: #4db3cf;
}